.Navigation ul {
  display: -ms-flexbox;
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 0px;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 0px;
}
.Navigation ul li {
  font-weight: 400;
  padding: 10px 7px;
  font-size: 14px;
  -webkit-transition: background .125s ease;
  -o-transition: background .125s ease;
  transition: background .125s ease;
  border-radius: 3px;
  list-style-type: none;
}
.Navigation ul li:hover {
  background: #e50914;
  cursor: pointer;
}



